.name {
   text-align: center;
}

.change-name {
   display: inline-block;
   height: 10px;
   width: 10px;
   border: 1px solid blue;
   background-color: blue;
   margin-left: 5px;
   cursor: pointer;
}

.health {
   display: inline-block;
   width: 180px;
   height: 7px;
   border: 1px solid grey;
   border-radius: 3px;
   background-color: rgb(4, 100, 16);
   margin: 0 5px 0 0;
}

.hp {
   font-size: 15px;
}

.image {
   width: 120px;
   height: 220px;
   /* border: 1px solid black; */
}

img {
   width: 100%;
   height: 100%;
   object-fit: cover;
}

.player {
   margin-top: 5px;
   display: flex;
}

.weapon, .shield {
   width: 60px;
   height: 65px;
}

.armor {
   width: 60px;
   height: 90px;
}

